import axios from 'axios';
import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    sampleBlogCards:[
      {blogTitle:"Blog Card #1",blogCoverPhoto:"stock-1",blogDate:"May 1,2021"},
      {blogTitle:"Blog Card #2",blogCoverPhoto:"stock-2",blogDate:"May 1,2021"},
      {blogTitle:"Blog Card #3",blogCoverPhoto:"stock-3",blogDate:"May 1,2021"},
      {blogTitle:"Blog Card #4",blogCoverPhoto:"stock-4",blogDate:"May 1,2021"},
    ],
    editPost: null,
    userToken:null,
    roles:{
      superAdmin :null,
      admin : null,
      user:null
    }
  },
  getters:{
    isLogin(state){
      return !!state.userToken;
    },
    isSuperAdmin(state){
      return !!state.roles.superAdmin
    },
    isAdmin(state){
      return !!state.roles.admin
    }
  },
  mutations: {
    toggleEditPost(state,payload){
      state.editPost = payload;
    },
    setUserToken(state,userToken){
      state.userToken = userToken;
      localStorage.setItem('userToken',userToken);
    },
    setRole(state,role){
      if(role.role === 'Super Admin'){
        state.roles.superAdmin = true;
        console.log('role is : '+state.superAdmin);
      }else if(role.role === 'Admin'){
        state.roles.admin = true;
      }else{
        state.roles.user = true;
      }
    }
    
  },
  actions: {
    async LoginUser({commit},payload){
      await axios.post('http://127.0.0.1:8000/api/auth/login',payload)
      .then(res => {
        commit('setUserToken',res.data.access_token)
        commit('setRole',res.data.user.role)
      }).catch(err =>{
        console.log(err);
      })
    }
  },
  modules: {
  }
})
