<template>
  <div class="app-wrapper">
    <div class="app">
      <Navigation v-if="!Navigation" />
      <router-view />
    </div>
  </div>
</template>

<script>
// import axios from 'axios';
import Navigation from "./components/Navigation.vue"
export default {
  name: "app",
  components: {
    Navigation,
  },
  data() {
    return {
      Navigation:null,
    };
  },
  created() {
    this.checkRoute();
  },
  mounted() {},
  methods: {
    checkRoute() {
      if(this.$route.name==="Login" || this.$route.name==="Register"|| this.$route.name==="Register"){
        this.Navigation = true;
        return;
      }
      this.Navigation = false;
    }
  },
  watch: {
    $route(){
      this.checkRoute()
    }
  },
};
</script>

